
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
html, body, #app {
  width: 100%;
  height: 100%;
  margin: 0;
}
a {
  text-decoration: none;
  color: #000;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.layout {
  height: 100%;
  position: relative;
  padding: 3em 0 0;
  box-sizing: border-box;
}
.layout:before {
    content: '';
    display: block;
    width: 100%;
    height: 3em;
    background: #dcdddd;
    position: absolute;
    top: 0;
}
.button {
  line-height: 2rem;
  background: #87cdc9;
  color: #fff;
  border: none;
  border-radius: 2rem;
  text-decoration: none;
  display: inline-block;
  margin: 1rem;
  font-weight: 700;
  font-size: 1.5rem;
}
.button.large {
    padding: 1rem 2rem;
}
.button.italic {
    font-style: italic;
}
.button.small {
    padding: 0.5rem 1.5rem;
}



