
@-webkit-keyframes leftHeart {
0% {
    margin-left: 20rem;
}
25% {
    margin-left: 16rem;
}
50% {
    margin-left: 12rem;
    opacity: 0;
}
100% {
    margin-left: -2rem;
    opacity: 1;
}
}
@-webkit-keyframes rightHeart {
from {
    margin-right: 12rem;
    opacity: 0;
}
to {
    margin-right: -2rem;
    opacity: 1;
}
}
.heart[data-v-80a6ca62], .seed[data-v-80a6ca62] {
  top: 35%;
  margin-top: -7rem;
  height: 14rem;
  position: fixed;
}
.heart[data-v-80a6ca62] {
  width: 7rem;
  background: url(../assets/farmer/heart.png) no-repeat center;
  background-size: 100%;
}
.heart.left[data-v-80a6ca62] {
    left: 50%;
    -webkit-animation: leftHeart 4s;
    margin-left: -2rem;
}
.heart.right[data-v-80a6ca62] {
    right: 50%;
    -webkit-animation: rightHeart 2s;
    margin-right: -2rem;
}
.seed[data-v-80a6ca62] {
  width: 5rem;
  margin-top: -5.5rem !important;
  left: 50%;
  margin-left: -2.5rem;
  background: url(../assets/seed/helloSeed.png) no-repeat center;
  background-size: 100%;
  z-index: 2;
}
p[data-v-80a6ca62] {
  position: fixed;
  top: 55%;
  width: 100%;
  text-align: center;
  left: 0;
}



